<template>
  <el-main class="main" v-loading="pageLoading" element-loading-text="拼命加载中">
    <el-row v-show="!pageLoading">
      <i class="el-icon-success" style="font-size: 80px;color: rgb(25, 190, 107);" v-if="status"/>
      <i class="el-icon-error" style="font-size: 80px;color: rgb(245, 108, 108);" v-if="!status"/>
      <p>{{ status ? '邮箱验证成功' : '邮箱验证失败' }}</p>
      <p>{{ message }}</p>
      <el-row class="button-row">
        <el-button type="primary" @click="$router.push('/')">回到首页</el-button>
      </el-row>
    </el-row>
  </el-main>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "UserEmailVerify",
    props: {
      code: String
    },
    data() {
      return {
        pageLoading: true,
        status: false,
        message: ''
      }
    },
    methods: {
      emailVerify() {
        if (this.code.length === 0) {
          this.$notify.error({
            title: '访问错误',
            message: '请提供邮箱激活码'
          });
        }
        axios.get('/email/active/' + this.code)
          .then((resp) => {
            if (resp.data.status === 200) {
              this.status = true;
              this.message = resp.data.message;
            } else {
              this.status = false;
              this.message = resp.data.message;
            }
          })
          .catch(() => {
            this.$notify.error({
              title: '邮箱验证失败',
              message: '服务器连接错误，请稍后重试'
            });
          })
          .finally(() => {
            this.pageLoading = false
          })
      }
    },
    mounted() {
      this.emailVerify();
      document.title = "邮箱激活";
    }
  }
</script>

<style scoped>
  .main {
    padding-top: 80px;
    text-align: center;
  }

  .button-row {
    margin-top: 50px;
  }
</style>
